@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);


*{
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    overflow-y: hidden;
}
body{
    margin: 0;
    padding: 0;
}

.Home{
    margin: 0;
    padding: 0;
    background-color: #2F3778;
    color: white;
    display: flex;
    height: 100vh;
    position: relative;
}

.home-info{
    width: 34%;
    left: 180px;
    top: 20%;
    position: relative;
}
.home-info::before{
    content: '';
    position: absolute;
    height: 100%;
    width: 3px;
    background-color: #326A8F;
    top: -20%;
    left: -10%;
}
.home-info h3{
    font-size: 2rem;
    margin-bottom: 2rem;
}

.home-info p {
    font-size: 0.65em;
}

.home-info li{
    list-style: none;
    margin: 1rem;
    margin-left: 0;
    font-size: 13px;
}

.span-item-home{
    font-size: 25px;
    margin-left: 10px;
    color:#42CDC0 ;
}

.home-page-images{
    position: absolute;
    right: 100px;
    top: 18%;
    
}

.Navbar{
    margin: 0;
    padding: 0;
    height: auto;
    background-color: #ececec;
    display: flex;
    position: relative;
    color: #2F3778;
}

.nav-items{
    width: 60%;
    align-self: center;
    position: absolute;
    right: 0;
}

.nav-items ul{
    display: flex;
    justify-content: space-around;
    list-style: none;
}
.Navlinks{
    position: relative;
    cursor: pointer;
    animation: navLinkFade .5s ease forwards 1/7+1s;
}
.Navlinks::after{
    content: '';
    width: 0;
    background-color: #2F3778;
    height: 2px;
    position: absolute;
    left: 50%;
    bottom: 0;
    transition:  transform ease 0.2s ;
    transform: translate(-50%);
    transition: width 0.3s;
}

.Navlinks:hover::after{
    width: 100%;
}
.logo img{
    height: 3rem;
    margin-left: 3rem;
}

.Navbar-Button{
    color: white;
    height: 22px;
    width: 7rem;
    background: #2F3778;
    border: none;
    border-radius: 29px;
}
